<template>
    <div class="page-container">
        <div class="product-container">
            <div style="display: flex;justify-content: space-between">
                <div style="display: flex;">
                    <div class="date-box">
                        <v-btn depressed :class="'white-button '+dateClass.day" @click="setDateType('day')">
                            일간
                        </v-btn>
                        <v-btn depressed :class="'white-button '+dateClass.week" @click="setDateType('week')">주간
                        </v-btn>

                        <v-btn depressed :class="'white-button '+dateClass.month" @click="setDateType('month')">월간
                        </v-btn>

                        <v-btn depressed :class="'white-button '+dateClass.year" @click="setDateType('year')">연간
                        </v-btn>

                    </div>
                    <div v-if="dateClass.day=='active' || dateClass.week=='active'" class="month-box">
                        <div class="day-item">
                            <v-menu
                                    ref="dateMenu1"
                                    v-model="dateMenu1"
                                    transition="scale-transition"
                                    offset-y
                                    max-width="290px"
                                    min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-text-field
                                            v-model="stdate"
                                            class="day-item"
                                            dense
                                            readonly
                                            v-bind="attrs"
                                            v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker
                                        v-model="stdate"
                                        @input="dateMenu1 = false"
                                ></v-date-picker>
                            </v-menu>
                        </div>
                        <div class="term"> ~</div>
                        <div class="day-item">
                            <v-menu
                                    ref="dateMenu2"
                                    v-model="dateMenu2"
                                    transition="scale-transition"
                                    offset-y
