                                    ></v-text-field>
                                </template>
                                <v-date-picker
                                        v-model="endate"
                                        @input="dateMenu2 = false"
                                ></v-date-picker>
                            </v-menu>
                        </div>
                    </div>
                    <div v-else-if="dateClass.month=='active'" class="month-box">
                        <div class="day-item">
                            <v-menu
                                    ref="menu1"
                                    v-model="menu1"
                                    transition="scale-transition"
                                    offset-y
                                    max-width="290px"
                                    min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-text-field
                                            v-model="stmonth"
                                            class="day-item"
                                            dense
                                            readonly
                                            suffix="월"
                                            v-bind="attrs"
                                            v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker
                                        v-model="stmonth"
                                        type="month"
                                        @input="menu1 = false"
                                ></v-date-picker>
                            </v-menu>
                        </div>
                        <div class="term"> ~</div>
                        <div class="day-item">
                            <v-menu
                                    ref="menu2"
                                    v-model="menu2"
                                    transition="scale-transition"
                                    offset-y
                                    max-width="290px"
                                    min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-text-field
                                            v-model="enmonth"
                                            class="day-item"
                                            dense
                                            readonly
                                            suffix="월"
                                            v-bind="attrs"
                                            v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker
                                        v-model="enmonth"
                                        type="month"
                                        @input="menu2 = false"
                                ></v-date-picker>
                            </v-menu>

                        </div>
                    </div>
                    <div v-else class="year-box">
                        <v-select class="year-item"
                                  v-model="styear"
                                  dense
                                  :items="yearList">

                        </v-select>
                        <span> ~ </span>
                        <v-select class="year-item"
                                  dense
                                  v-model="enyear"
                                  :items="yearList">

                        </v-select>
                    </div>
                    <div class="button-box">
                        <v-btn depressed class="white-button submit-button" @click="search()">
                            <font-awesome-icon icon="fa-solid fa-magnifying-glass" style="margin-right:6px;"/>
                            검색
                        </v-btn>
                    </div>
                </div>
                <div style="padding-right:16px;">
                    <v-btn type="button" depressed class="black-button" @click="excelDateDown()">
                        다운로드현황 엑셀다운로드
                    </v-btn>
                </div>
            </div>
            <div class="graph-box">

                <LineChartGenerator v-if="chartShow"
                                    :chart-options="chartOptions"
                                    :chart-data="chartData"
                                    :height="height"

                />
                <v-progress-circular v-else
                                     :size="100"
                                     class="progress-box"
                                     color="#DD1B16"
                                     indeterminate
                ></v-progress-circular>
            </div>
            <div class="status-box">
                <div class="left-table">
                    <div class="table-title">
                        누적 다운로드
                        <span class="number blue-text">총 {{util.numberFormat(total)}}건</span>
                        <span class="date">{{label}}</span>
                    </div>
                    <div class="data-box">
                        <ul class="graph-ul">
                            <li v-for="(item,index) in totalList">
                                <div class="gbox" :style="{background:item.color}" v-if="index%2==0">
                                    <div class="gtop">
                                        0{{index+1}}
                                    </div>
                                    <div class="gmiddle">
                                        <div class="gname">
                                            {{item.orgFileName}}
                                        </div>
                                        <div class="gdown">
                                            <font-awesome-icon style="font-size:12px" icon="fa-solid fa-download"/>
                                            {{util.numberFormat(item.total)}}
                                        </div>
                                    </div>
                                    <div class="gbottom">
                                        {{item.per}}
                                    </div>
                                </div>
                                <div class="reverse-gbox" :style="{background:item.color}" v-else>
                                    <div class="gbottom">
                                        {{item.per}}
                                    </div>
                                    <div class="gmiddle">
                                        <div class="gdown">
                                            <font-awesome-icon style="font-size:12px" icon="fa-solid fa-download"/>
                                            {{util.numberFormat(item.total)}}
                                        </div>
                                        <div class="gname">
                                            {{item.orgFileName}}
                                        </div>

                                    </div>
                                    <div class="gtop">
                                        0{{index+1}}
                                    </div>

                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="right-table">
                    <div class="data-box">
                        <table class="table" cellspacing="0" cellpadding="0">
                            <thead>
                            <tr>
                                <th>파일명</th>
                                <th style="width:100px"> 누적다운로드</th>
                                <th style="width:100px"> 지분율</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr v-for="item in totalList">
                                <td>
                                    <span :style="{background:item.color}"></span>
                                    {{item.orgFileName}}
                                </td>
                                <td>{{item.total}}</td>
                                <td>{{item.per}}</td>
                            </tr>
                            </tbody>
                        </table>

                    </div>
                    <div style="padding:30px 0 0 0;text-align: right;">
                        <v-btn type="button" depressed class="black-button" @click="excelDown()">
                            다운로드순위 엑셀다운로드
                        </v-btn>
                    </div>
                </div>
            </div>
        </div>
        <div style="display: none;">

            <table border="1" id="excelDateTable">
                <thead>
                <tr>
                    <th>기간</th>
                    <th v-for="data in excelDateList">
                        {{data.label}}
                    </th>

                </tr>
                </thead>
                <tbody>
                <tr v-for="(date,index) in this.tableDayList">
                    <td>{{date}}</td>
                    <td v-for="data in excelDateList">{{data.data[index]}}</td>
                </tr>
                </tbody>
            </table>
            <table border="1" id="excelTable">
                <thead>
                <tr>
                    <th>파일명</th>
                    <th style="width:100px"> 누적다운로드</th>
                    <th style="width:100px"> 지분율</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="item in totalList">
                    <td>
                        <span :style="{background:item.color}"></span>
                        {{item.orgFileName}}
                    </td>
                    <td>{{item.total}}</td>
                    <td>{{item.per}}</td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>


<script>

    import {Line as LineChartGenerator} from 'vue-chartjs/legacy'

    import {
        Chart as ChartJS,
        Title,
        Tooltip,
        Legend,
        LineElement,
        LinearScale,
        CategoryScale,
        PointElement
    } from 'chart.js'

    ChartJS.register(
        Title,
        Tooltip,
        Legend,
        LineElement,
        LinearScale,
        CategoryScale,
        PointElement
    )


    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_GET_BOARD_ARTICLE_LIST,
        API_ADMIN_GET_STATISTICS_DOWN,
    } from '@/config/admin/urls'
    import UTIL from "@/common/util";

    export default {
        components: {
            LineChartGenerator
        },
        watch: {
            cmd(val) {
                this.setInit();
            }
        },
        data() {
            return {

                util: UTIL,
                chartData: {
                    labels: [],
                    datasets: []
                },
                chartOptions: {
                    responsive: true,
                    maintainAspectRatio: false
                },
                chartShow: false,
                height: 400,
                dateClass: {
                    year: '',
                    month: '',
                    week: '',
                    day: 'active'
                },
                dateType: 'day',
                dateMenu1: false,
                dateMenu2: false,
                stdate: '',
                endate: '',

                stmonth: '',
                enmonth: '',
                menu1: false,
                menu2: false,
                yearList: [],
                styear: '',
                enyear: '',
                dayList: [],

                total: 0,
                label: '',
                totalList: [],
                colorList: [
                    '#C62828',
                    '#4A148C',
                    '#3949AB',
                    '#1E88E5',
                    '#00ACC1',
                    '#00897B',
                    '#43A047',
                    '#827717',
                    '#FFB300',
                    '#F4511E',
                ],
                monthAllView: false,
                excelDateList: [],
                excelList: [],
                tableDayList: [],

            };
        },
        created() {
            this.setInit();
        },
        methods: {
            setInit() {
                const date = new Date();
                const year = date.getFullYear();
                const day = (date.getDate() < 10) ? '0' + date.getDate() : date.getDate();
                for (let y = year; y >= 2010; y--) {
                    this.yearList.push({text: y + '년', value: y})
                }
                this.styear = year - 5;
                this.enyear = date.getFullYear();

                let month = parseInt(date.getMonth()) + 1;
                if (month < 10) month = '0' + month;


                const predate = new Date(year, parseInt(date.getMonth() - 1), date.getDate());
                const lastdate = new Date(year, date.getMonth(), 0);
                const lastDay = lastdate.getDate();


                let premonth = parseInt(predate.getMonth()) + 1;
                if (premonth < 10) premonth = '0' + premonth;
                const preDay = (date.getDate() > lastDay) ? lastDay : day;

                this.stmonth = predate.getFullYear() + '-' + premonth;
                this.enmonth = year + '-' + month;

                this.stdate = year + '-' + premonth + '-' + preDay;
                this.endate = year + '-' + month + '-' + day;

                this.getData();
            },

            getData() {
                const frm = this.setDataParams();

                switch (this.dateType) {
                    case 'day':
                    case 'week':
                        this.label = '[' + this.stdate + ' ~ ' + this.endate + ']';
                        break;
                    case 'month':
                        this.label = '[' + this.stmonth + ' ~ ' + this.enmonth + ']';
                        break;
                    case 'year':
                        this.label = '[' + this.styear + ' ~ ' + this.enyear + ']';
                        break;
                }
                apiService.post(API_ADMIN_GET_STATISTICS_DOWN, frm, (res) => {
                    this.setGraph(res)
                })
            },
            setDataParams() {
                this.chartShow = false;
                this.setDateList();
                const frm = {
                    dateType: this.dateType,
                    styear: this.styear.toString(),
                    enyear: this.enyear.toString(),
                    stmonth: this.stmonth.toString(),
                    enmonth: this.enmonth.toString(),
                    stdate: this.stdate.toString(),
                    endate: this.endate.toString(),
                }
                return frm;
            },
            setDateList() {
                let lastDay;
                let startDay;

                let stNumber = 0;
                let enNumber = 0;
                let diffDate = '';
                this.tableDayList = [];
                switch (this.dateType) {
                    case 'day':
                    case 'week':
                        let stdates = this.stdate.split('-');
                        let stDayDate = new Date(stdates[0], stdates[1] - 1, stdates[2]);
                        let endates = this.endate.split('-');
                        let enDayDate = new Date(endates[0], endates[1] - 1, endates[2]);
                        diffDate = enDayDate.getTime() - stDayDate.getTime();
                        if (diffDate < 0) {
                            let params = {
                                message: '시작일은 종료일보다 같거나 작아야 합니다',

                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return;
                        }
                        if (this.dateType == 'day') {
                            let diffDateNumber = Math.abs(diffDate / (1000 * 60 * 60 * 24));
                            if (diffDateNumber > 31) {
                                let params = {
                                    message: '일별 통계는 기간 차이가 31일 이내여야 합니다 ',

                                }
                                this.$eventBus.$emit('modalOpen', params);
                                return;
                            }
                            this.dayList = []
                            let stMonth = stDayDate.getMonth() + 1;
                            let enMonth = enDayDate.getMonth() + 1;
                            let checkMonthFlag = false;
                            let lastDayDate = new Date(stdates[0], stdates[1], 0);
                            lastDay = lastDayDate.getDate();
                            let startDay = stDayDate.getDate();
                            let endDay = enDayDate.getDate();

                            if (stMonth != enMonth) {
                                for (let k = startDay; k <= lastDay; k++) {
                                    let date = (k < 10) ? '0' + k : k;
                                    let dateMonth = (stMonth < 10) ? '0' + stMonth : stMonth;
                                    this.dayList.push(dateMonth + '/' + date)
                                    this.tableDayList.push(stdates[0] + '-' + dateMonth + '-' + date)
                                }
                                startDay = 1;
                            }
                            for (let k = startDay; k <= endDay; k++) {
                                let date = (k < 10) ? '0' + k : k;
                                let dateMonth = (enMonth < 10) ? '0' + enMonth : enMonth;
                                this.dayList.push(dateMonth + '/' + date)
                                this.tableDayList.push(endates[0] + '-' + dateMonth + '-' + date)

                            }
                        } else {
                            this.dayList = []
                            let weekText = ['일', '월', '화', '수', '목', '금', '토'];
                            for (let k = 0; k <= 6; k++) {
                                this.dayList.push(weekText[k])
                                this.tableDayList.push(weekText[k])
                            }

                        }
                        break;
                    case 'month':
                        stNumber = parseInt(this.stmonth.replace('-', ''));
                        enNumber = parseInt(this.enmonth.replace('-', ''));
                        diffDate = enNumber - stNumber;
                        if (diffDate < 0) {
                            let params = {
                                message: '시작일은 종료일보다 같거나 작아야 합니다',

                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return;
                        }
                        let stMonths = this.stmonth.split('-');
                        let enMonths = this.enmonth.split('-');
                        let stYear = parseInt(stMonths[0]);
                        let enYear = parseInt(enMonths[0]);
                        let startMonth = parseInt(stMonths[1]);
                        let endMonth = parseInt(enMonths[1]);

                        this.dayList = [];
                        if (diffDate >= 99) {
                            this.monthAllView = true;
                            for (let k = 1; k <= 12; k++) {
                                let date = (k < 10) ? '0' + k : k;
                                this.dayList.push(date)
                                this.tableDayList.push(stYear + '-' + date)
                            }
                        } else {
                            this.monthAllView = false;
                            if (stYear != enYear) {
                                for (let k = startMonth; k <= 12; k++) {
                                    let date = (k < 10) ? '0' + k : k;
                                    let dateYear = (stYear < 10) ? '0' + stYear : stYear;
                                    this.dayList.push(dateYear + '/' + date)
                                    this.tableDayList.push(dateYear + '-' + date)
                                }
                                startMonth = 1;
                            }
                            for (let k = startMonth; k <= endMonth; k++) {
                                let date = (k < 10) ? '0' + k : k;
                                let dateYear = (stYear < 10) ? '0' + stYear : stYear;
                                this.dayList.push(dateYear + '/' + date)
                                this.tableDayList.push(dateYear + '-' + date)

                            }
                        }
                        break;
                    case 'year':
                        if (parseInt(this.styear) > parseInt(this.enyear)) {
                            let params = {
                                message: '시작일은 종료일보다 같거나 작아야 합니다',

                            }
                            this.$eventBus.$emit('modalOpen', params);
                            return;
                        }
                        lastDay = this.enyear;
                        startDay = this.styear;
                        this.dayList = []
                        for (let k = startDay; k <= lastDay; k++) {
                            let date = (k < 10) ? '0' + k : k;
                            this.dayList.push(date)
                            this.tableDayList.push(date)
                        }
                        break;
                }

            },
            setDateType(code) {
                this.dateType = code;
                for (const key in this.dateClass) {
                    if (key == code) this.dateClass[key] = 'active';
                    else this.dateClass[key] = '';

                }
            },
            search() {
                this.getData();
            },
            setGraph(res) {

                let total = 0;
                for (const item of res.downTotalData) {
                    total += item.total;
                }
                this.total = total;
                let index = 0;
                this.totalList = [];
                const fileKeyDatas = {};
                const fileKeyNames = {};
                for (const key in res.downData) {
                    fileKeyNames[key] = res.downData[key][0]['org_file_name']
                }
                for (const item of res.downTotalData) {
                    item.orgFileName = fileKeyNames[item['file_name']]
                    let per = (item.total / total) * 100;
                    item.per = parseInt(per) + '%';
                    item.color = this.colorList[index]
                    this.totalList.push(item)
                    fileKeyDatas[item['file_name']] = {
                        fileName: item.orgFileName,
                        list: {}
                    };
                    index++;
                }


                this.chartData.labels = [];
                for (const key in res.downData) {
                    if (fileKeyDatas[key]) {
                        for (let item of res.downData[key]) {
                            let indexDateKey = this.getIndexDateKey(item)
                            if (!fileKeyDatas[key]['list'][indexDateKey]) fileKeyDatas[key]['list'][indexDateKey] = [];
                            fileKeyDatas[key]['list'][indexDateKey].push(item)
                        }
                    }
                }
                let dataList = {};
                let dateIndexKey = '';
                index = 1;
                for (const dayKey of this.dayList) {
                    this.chartData.labels.push(dayKey);
                    if (this.dateType == 'week') {
                        dateIndexKey = index;
                    } else {
                        dateIndexKey = dayKey;
                    }
                    for (const key in fileKeyDatas) {
                        if (!dataList[key]) dataList[key] = [];
                        if (fileKeyDatas[key]['list'][dateIndexKey]) {
                            for (const item of fileKeyDatas[key]['list'][dateIndexKey]) {
                                dataList[key].push(item.total)
                            }
                        } else {
                            dataList[key].push(0)
                        }

                    }
                    index++;
                }
                this.chartData.datasets = [];
                this.excelDateList = [];
                let data = {};
                index = 0;
                for (const key in fileKeyDatas) {
                    data = {
                        label: fileKeyDatas[key].fileName,
                        backgroundColor: this.colorList[index],
                        data: dataList[key]
                    }
                    this.excelDateList.push(data)
                    this.chartData.datasets.push(data);
                    index++;
                }
                console.log(this.excelDateList)

                this.chartShow = true;
            },
            getIndexDateKey(item) {
                let checkDate;

                switch (this.dateType) {
                    case 'day':
                        checkDate = item.date.split('-')
                        return checkDate[1] + '/' + checkDate[2];
                        break;
                    case 'month':

                        if (this.monthAllView) {
                            return item.date;
                        } else {
                            checkDate = item.viewDate.split('-')
                            return checkDate[0] + '/' + checkDate[1];
                        }
                        break;
                    case 'week':
                    case 'year':
                        return item.date;
                        break;
                }
            },
            excelDateDown() {
                this.excelDownActive('date')
            },
            excelDown() {
                this.excelDownActive('list')
            },
            excelDownActive(downType) {
                const wb = window.XLSX.utils.book_new();
                let fileName = '다운로드현황';
                let sheetName = '다운로드현황';
                let tableId = 'excelDateTable'

                if (downType != 'date') {
                    fileName = '다운로드순위[';
                    sheetName = '다운로드순위';
                    tableId = 'excelTable';

                } else {
                    switch (this.dateType) {
                        case 'day':
                            fileName = fileName + '[일간_';
                            break;
                        case 'week':
                            fileName = fileName + '[주간_';
                            break;
                        case 'month':
                            fileName = fileName + '[월간_';
                            break;
                            break;
                        case 'year':
                            fileName = fileName + '[연간_';
                            break;
                            break;
                    }
                }
                fileName = fileName + this.stdate + '_' + this.endate + '].xlsx';


                const workSheet = window.XLSX.utils.table_to_sheet(document.getElementById(tableId));
                // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
                window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);
                // step 4. 엑셀 파일 만들기
                const wbout = window.XLSX.write(wb, {bookType: 'xlsx', type: 'binary'});
                // step 5. 엑셀 파일 내보내기
                window.saveAs(new Blob([this.s2ab(wbout)], {type: "application/octet-stream"}), fileName);

            },
            s2ab(s) {
                const buf = new ArrayBuffer(s.length); //convert s to arrayBuffer
                const view = new Uint8Array(buf);  //create uint8array as viewer
                for (var i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF; //convert to octet
                return buf;
            },
        },

    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/style/common.scss';

    .product-container {
        width: 100%;
        padding: 30px;
        border: solid 1px #999;
        background: #fff;

    }

